.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #333;
  color: #fff;
  overflow: hidden;
}

.App-main {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 90%;
  background-color: #444;
  border-radius: 0;
  box-shadow: none;
  overflow: hidden;

}

.editor,
.previewer {
  flex: 1;
  padding: 20px;
}

.editor {
  
  background-color: #555;
  color: #fff;
  font-size: 20px;
  
}

.previewer {
  background-color: #666;
  color: #fff;
  overflow: scroll;
  

}
.previewer header{
  background-color: #666;
}
header{
  background-color: #555;
  display: flex;
  justify-content: center;
  align-items: end;
  width: 100%;
  height: 10%;
}